<template>
    <div class="main">
        <div class="container pz10">
            <div class="main_title pz10">
                <h4>笔记</h4>
            </div>
            <div class="main1 pz10">
                <div class="main1_listtitle">
                    <span>{{datas.title}}</span>
                </div>
                <div class="main1_box">
                    <ul class="flexbox">
                        <li class="flex1" v-for="item in datas.list">
                            <h4>
                                <router-link :to="{path:item.url}">{{item.name}}</router-link>
                            </h4>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'article_list',
    data(){
        return{
            datas:[]
        }
    },
    created(){
        
    },
    mounted(){
        // console.log(this.$route.params.title);
        var titles = this.$route.params.title;
        var alldatas = this.all_link.all_list;
        switch (titles) {
            case 'Git':
                this.datas = alldatas.git;
                break;
            case 'Vue':
                this.datas = alldatas.vue;
                break;
            case 'JS':
                this.datas = alldatas.js;
                break;
            case 'Canvas':
                this.datas = alldatas.canvas;
                break;
            case 'mini':
                this.datas = alldatas.mini;
                break;
            case 'PHP':
                this.datas = alldatas.php;
                break;
            case 'Python':
                this.datas = alldatas.python;
                break;
            case 'NodeJS':
                this.datas = alldatas.nodejs;
                break;
            case 'AngularJS':
                this.datas = alldatas.angularjs;
                break;
            case 'MySQL':
                this.datas = alldatas.mysql;
                break;
            case 'Other':
                this.datas = alldatas.other;
                break;
            default:
                alert('没有数据！');
                break;
        }

    },
    methods:{

    },
    components: {
        // myHeader:myHeader
    },
}
</script>
<style>
     /* @import'swiper/dist/css/swiper.min.css'; */
</style>


